.intro {
	height: 100vh;

	&__container {
		height: 100%;
	}
}

.intro-slider {
	height: 100%;

	.swiper-wrapper {
		height: 100%;
	}

	&__item {
		height: 100%;
		display: flex;
		flex-direction: column;
		background-position: center;
		background-repeat: no-repeat;
		background-size: cover;
		padding-top: 1.5rem;
		padding-bottom: 4rem;
	}

	&__item-top {
		position: relative;
		padding-bottom: 1.5rem;
		margin-bottom: 3.7rem;

		&::after {
			position: absolute;
			left: 0;
			bottom: -2px;
			z-index: 2;
			height: 2px;
			width: 100%;
			background: linear-gradient(90deg, rgba(243, 106, 29, 0) 0%, #F36A1D 48.23%, rgba(243, 106, 29, 0) 99.27%);
			content: "";
		}
	}

	&__item-bottom {
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		flex-grow: 1;
	}

	&__item-heading {
		display: inline-block;
		font-weight: 700;
		font-size: 13.2rem;
		line-height: 15.5rem;
		letter-spacing: -0.05em;
		text-transform: uppercase;
	}

	&__item-text {
		font-size: 2rem;
		line-height: 2.6rem;
		letter-spacing: -0.05em;
		text-transform: uppercase;
		max-width: 50.1rem;
		overflow: hidden;
		display: -webkit-box;
		-webkit-line-clamp: 4;
		-webkit-box-orient: vertical;
	}

	&__likes {
		position: relative;
		z-index: 6;
		margin-top: auto;
		align-self: center;
		display: flex;
		flex-direction: column;
		align-items: center;
	}

	&__likes-btn {
		width: 7.2rem;
		height: 7.2rem;
		background-color: rgba(153, 153, 153, 0.2);
		cursor: pointer;
		margin-bottom: 1.3rem;
		transition: background-color var(--main-taransition);

		&:hover {
			background-color: rgba($color: #F36A1D, $alpha: 0.1);

			.intro-slider__likes-btn-icon {
				fill: #F7AA7F;
			}
		}

		&:active {
			.intro-slider__likes-btn-icon {
				fill: #F36A1D;
			}
		}

		&[disabled] {
			background: rgba(153, 153, 153, 0.2);
			pointer-events: none;

			.intro-slider__likes-btn-icon {
				fill: var(--color-dark-grey);
			}
		}
	}

	&__likes-btn-icon {
		width: 3.8rem;
		height: 3.6rem;
		fill: var(--color-dark-grey);
		transition: fill var(--main-taransition);
	}

	&__likes-count {
		font-weight: 700;
		font-size: 3.7rem;
		line-height: 1;
		color: var(--color-dark-grey);
	}

	&__likes-count-num {
		color: var(--color-orangre);
	}
}
